import React from 'react';

import { Grid, Paper, Typography } from '@material-ui/core'

const VideoItem = ({ video, onVideoSelect }) => {
    return (
        <Grid item xs={12}>
            <Paper style={{ display: 'flex', alignItems: 'center', marginRight: '10px', marginBottom: "5px", cursor: "pointer" }} onClick={() => onVideoSelect(video) }>
                <img style={{ marginRight: '20px', height: "100px"}} alt="thumbnail" src={video.snippet.thumbnails.medium.url}/>
                <Typography variant='subtitle1'><b>{video.snippet.title}</b></Typography>
            </Paper>
        </Grid>
    )
}

export default VideoItem;